import Vue from 'vue';
import Router from 'vue-router';
import Home from '@/components/Home';
import TeamsByWins from '@/components/TeamsByWins';
import TeamsByBoundries from '@/components/TeamsByBoundries';
import BatsmenTotalRuns from '@/components/BatsmanTotalRuns';
import BatsmenIndividualScores from '@/components/BatsmenIndividualScores';
import BatsmenBoundries from '@/components/BatsmenBoundries';
import BowlerTotalWickets from '@/components/BowlerTotalWickets';
import BowlerEconomy from '@/components/BowlerEconomy';
import AwardsMOTM from '@/components/AwardsMOTM';
import AwardsOrangeCaps from '@/components/AwardsOrangeCaps';
import AwardsPurpleCaps from '@/components/AwardsPurpleCaps';
import SeasonFinals from '@/components/SeasonFinals';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home,
    },
    {
      path: '/finals',
      name: 'Finals',
      component: SeasonFinals,
    },
    {
      path: '/team-wins',
      name: 'TeamsByWins',
      component: TeamsByWins,
    },
    {
      path: '/team-boundries',
      name: 'TeamsByBoundries',
      component: TeamsByBoundries,
    },
    {
      path: '/batsmen-total-runs',
      name: 'BatsmenTotalRuns',
      component: BatsmenTotalRuns,
    },
    {
      path: '/batsmen-individual-scores',
      name: 'BatsmenIndividualScores',
      component: BatsmenIndividualScores,
    },
    {
      path: '/batsmen-boundries',
      name: 'BatsmenBoundries',
      component: BatsmenBoundries,
    },
    {
      path: '/bowler-total-wickets',
      name: 'BowlerTotalWickets',
      component: BowlerTotalWickets,
    },
    {
      path: '/bowler-economy',
      name: 'BowlerEconomy',
      component: BowlerEconomy,
    },
    {
      path: '/awards-motm',
      name: 'AwardsMOTM',
      component: AwardsMOTM,
    },
    {
      path: '/awards-orange-caps',
      name: 'AwardsOrangeCaps',
      component: AwardsOrangeCaps,
    },
    {
      path: '/awards-purple-caps',
      name: 'AwardsPurpleCaps',
      component: AwardsPurpleCaps,
    },
  ],
});



